      <div class="match-star"><span>爱情长久指数</span><div v-bind:style="{width: stars[1] * 20 + '%'}"></div></div>
      <div class="match-star"><span>友谊和谐指数</span><div v-bind:style="{width: stars[2] * 20 + '%'}"></div></div>
      <div class="match-star"><span>职场人际指数</span><div v-bind:style="{width: stars[3] * 20 + '%'}"></div></div>
      <div class="content-body-head">
        <div v-touch:tap="contentTouchLeft" v-bind:class="{'active': contentIndex == 0}">谈情必读</div>
        <div v-touch:tap="contentTouchMiddle" v-bind:class="{'active': contentIndex == 1}">{{ horoType == 1 ? '小攻' : horoType == 2 ? '小T' : '男生' }}必读</div>
        <div v-touch:tap="contentTouchRight" v-bind:class="{'active': contentIndex == 2}">{{ horoType == 1 ? '小受' : horoType == 2 ? '小P' : '女生' }}必读</div>
      </div>
