<script>
export default {
  data () {
    return {
      headIndex: 0,
      headShow: false,
      labels: ['白羊座', '金牛座', '双子座', '巨蟹座', '狮子座', '处女座', '天秤座', '天蝎座', '射手座', '摩羯座', '水瓶座', '双鱼座'],
      show: false,
      horoLeft: this.$route.query.l,
      horoRight: this.$route.query.r,
      horoType: this.$route.query.t,
      rate: Math.round(Math.random() * 100),
      stars: [4, 3, 5, 3],
      contentIndex: 0,
      matchTextA: '当这两个坚强的自我第一次将他们的个人气味混和在一起时，往往都是热情洋溢的。他们确实会高兴地发现，在与那些过于拘谨的、古板保守的人，那些无趣的、令人扫兴的、沉默寡言的人，特别是那些冷淡的、难于接近的人在一起呆久了之后，现在所接触到的是个多么积极的、热情的、开放的人。毫不奇怪，双方感到的都是一种互相羡慕的冲动，一种共同的幸运的慰藉。因此，双方应该妥善处理感情问题，应该平心静气，抱一种设身处地、推已及人的态度。正如我们的孔老夫子所说的：“已所不欲，勿施于人。”西方的《圣经》上也有一条“金箴”，是耶稣训戒他的门徒时说的，他说：“无论何事，你们愿意人怎样待你们，你们也要怎样待人。”上面两条箴言，意义都一样，是说当你处理各种事时，不应只顾自己，不顾他人。这箴言也可以用在爱情上，因为爱情不是一场谁都可以得胜的比赛，不是一场可由谁操纵的战争，甚至也不是一件能够获取的奖品。爱情是一种相互赠予的礼物。爱情不可能用金钱买得，不可能用鞭子降服用铁链锁住。爱情是一种执着难解的至情，是心心相印，是心灵的完美结合。既爱对方，也就应该信任对方。',
      matchTextL: '这对人很容易彼此相熟，哪怕是在路口等绿灯这么一点时间，他们都能由不相识而交谈起来。他们都喜欢说，却不在意听对方说什么。而且，他们说了很多，却都向对方隐瞒了一些自认为最真实的东西。甚至他们婚后多年了，还各自暗暗发笑道：“天知道，这么久来，我还有一些事没告诉他(她)。”由于男女对方的情绪都比较容易起伏，所以彼此不要因此而指责对方。较明智的办法是鼓励对方放开情感而不要压抑，也鼓励自己向对方大胆倾吐想象。他们会相互理解，也能相互支持，哪怕明知对方只是幻想，但也值得一试。重要的不是结果，而是在尝试本身。',
      matchTextR: '眼泪是他们相处时经常出现的一部分，但眼泪也有治愈创伤的时候。当他以家庭的亲密和睦来驱走她的恶梦时，眼泪就会变为欢笑。当他拥着她，她进入梦乡后做的一定是美梦，因为那意味着她有安全感，一种摆脱记忆中童年的孤独的安全感。那时，他才知道她多么需要他，而当他在流泪……但她没有醒，所以她不知道他流泪了，而他也从不会告诉她。她有很多秘密，但他只有一个秘密——他的脆弱。他愿意跟随她到那个角落给她道歉，但他不知道如何去那儿，所以他必须等她从那个神秘的地方走出。当她恢复原来的状态时，他总是很高兴。当她为他做饭时，他便活跃起来，当她吻他时，她的发香使他兴奋。是建议一起外出旅行的时候了，他强烈的旅游欲重新复苏。她说：“我们一起去旅行吧。”于是他自信地制订起旅行计划。第二天早晨他们就出发了。'
    }
  },
  ready () {
    this.$set('show', true)
    Draggable.create('#result', {
      type: 'scrollTop',
      edgeResistance: 1,
      throwProps: true,
      dragClickables: true,
      lockAxis: true
    })
  },
  methods: {
    // contentLeft () {
    //   switch (this.contentIndex) {
    //     case 0:
    //       this.$set('contentIndex', 1)
    //       break
    //     case 1:
    //       this.$set('contentIndex', 2)
    //       break
    //     default:
    //   }
    // },
    // contentRight () {
    //   switch (this.contentIndex) {
    //     case 2:
    //       this.$set('contentIndex', 1)
    //       break
    //     case 1:
    //       this.$set('contentIndex', 0)
    //       break
    //     default:
    //   }
    // },
    contentTouchLeft () {
      this.$set('contentIndex', 0)
    },
    contentTouchMiddle () {
      this.$set('contentIndex', 1)
    },
    contentTouchRight () {
      this.$set('contentIndex', 2)
    }
  }
}
</script>

<style>
  .match-result {
    width: 100%;
    height: 100%;
    font-size: 14px;
    line-height: 25px;
    color: #ece5f3;
  }

  .match-result-transition {
    transition: all 0.5s ease;
    opacity: 1;
  }

  .match-result-enter, .match-result-leave {
    opacity: 0;
  }

  .match-result>div {
    min-height: 100%;
  }

  .match-result .content-head {
    height: 136px;
  }

  .match-result .content-head-topline-left,
  .match-result .content-head-topline-right,
  .match-result .corner-left,
  .match-result .corner-right {
    top: 8px;
  }

  .match-result .content-head-topline-left {
    padding-right: 0;
  }

  .match-result .content-head-topline-right {
    padding-left: 0;
  }

  .match-result .content-body {
    padding-top: 136px;
    position: static;
    overflow: hidden;
  }

  .match-result .content-head-middleline-left, .match-result .content-head-middleline-right {
    top: 24px;
    height: 100px;
  }

  .match-result .content-body-border-left, .match-result .content-body-border-right {
