    width: 38px;
    height: 38px;
    border-radius: 100%;
    background: rgba(149, 93, 188, 0.5);
  }

  .match-icon-left {
    left: 30px;
  }

  .match-icon-right {
    right: 30px;
  }

  .match-icon-left:before, .match-icon-right:before {
    content: '';
    display: block;
    width: 46px;
    height: 46px;
    margin-left: -5px;
