    background-repeat: repeat-x;
    background-size: contain;
    background-position: 0 0;
  }

  .match-star:nth-of-type(2n+1) {
    transform: translateX(-100%);
    margin-left: -8%;
  }

  .match-star:nth-of-type(2n) {
    margin-left: 8%;
  }

  .match-star:nth-of-type(3),
  .match-star:nth-of-type(4)  {
    top: 190px;
  }

  .match-star span {
    display: block;
    line-height: 15px;
    width: 95px;
    position: absolute;
    top: -20px;
    left: -10px;
  }

  .match-star div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/match-star-selected.png);
    background-repeat: repeat-x;
    background-size: contain;
    background-position: 0 0;
  }

  .content-body-head {
    width: 100%;
    height: 29px;
    margin-top: 135px;
    padding: 0 24px;
    position: relative;
  }

  .content-body-head:before {
    content: '';
    display: block;
    position: absolute;
    box-sizing: border-box;
    top: 0;
    bottom: 0;
    left: 24px;
    right: 24px;
    margin: auto;
    border-radius: 3px;
    background: #9e95b3;
    opacity: 0.3;
    z-index: -1;
  }

  .content-body-head div {
    display: inline-block;
    line-height: 29px;
    width: 32%;
    position: relative;
    color: #9e95b3;
    transition: all 0.5s ease;
  }

  .content-body-head div.active {
