  }

  .content-body-head div:nth-of-type(1):before,
  .content-body-head div:nth-of-type(3):before {
    content: '';
    display: block;
    position: absolute;
    top: 7px;
    left: -1px;
    width: 1px;
    height: 15px;
    background: #43385e;
  }

  .content-body-head div:nth-of-type(1):before {
    left: auto;
    right: -1px;
  }

  .match-text {
    position: relative;
    width: 300%;
    min-height: 1px;
  }

  .match-text-1 {
    transform: translateX(-33.3333%);
  }

  .match-text-2 {
    transform: translateX(-66.6666%);
  }

  .match-text>div {
    width: 33.3333%;
    min-height: 1px;
    float: left;
  }

  .match-text-transition {
    width: 100%;
    padding: 20px 28px 10px;
    text-align: justify;
    text-indent: 28px;
    transition: opacity 0.2s linear;
    opacity: 1;
  }

  .match-text-enter, .match-text-leave {
    opacity: 0;
  }
</style>



